╚═╝     ╚═╝  ╚═╝ ╚═════╝ ╚═╝  ╚═══╝╚══════╝
*/
@media (max-width: 991px) {
  .schedule__entry .schedule__content,
  .schedule__entry .schedule__time {
    z-index: 1; }
  .schedule__entry:after {
    display: none; } }

